import React from 'react';
import { useIsWindowOpen } from '../Hooks';
import { Link, useRouteMatch } from "react-router-dom";
import { useDispatch } from 'react-redux';
import { setWindowIsOpen } from '../store/actions';

export default function PortfolioItem({ view, image, title, tags }) {
  let isWindowOpen  = useIsWindowOpen();
  let { url } = useRouteMatch();
  const dispatch = useDispatch();

  const handleKeyPress = (e) => e.key === 'Enter' ? openView() : null;

  const openView = () => {
    dispatch(setWindowIsOpen(true));
    window.scrollTo(0, 0);
  }
  
  return(
    <Link 
      className="portfolio-item"
      to={`${url}${view}`}
      onClick={() => openView()}
      aria-hidden={isWindowOpen}
      tabIndex={isWindowOpen ? '-1' : '0'}
      role="navigation"
      onKeyPress={(e) => handleKeyPress(e)}
    >
      <div className="portfolio-item__img-wrapper">
        <div className="gradient-background"/>
        <img 
          src={image}
          alt={title}
        />
      </div>
      <div className="portfolio-item__description">
        <h3>{title}</h3>
        <p>{tags}</p>
      </div>
    </Link>
  )
}