import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { setWindowIsOpen } from '../store/actions';
import { Tabs } from '../Constants';
import closeIcon from '../res/close.svg';

export default function WindowWrapper({ content }) {
  const handleClick = (e) => e.target.className === 'background' ? closeView() : null;
  const handleKeypress = (e) => e.key === 'Enter' ? closeView() : null;
  let history = useHistory();
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(setWindowIsOpen(true));
  }, []);

  const closeView = () => {
    history.push(Tabs.PORTFOLIO);
    dispatch(setWindowIsOpen(false));
  }

  return (
    <div 
      className="background" 
      onClick={(e) => handleClick(e)}
    >
      <div className="window" role="dialog">
        <img
          onKeyPress={(e) => handleKeypress(e)}
          alt="Close"
          className="close-icon"
          role="button"
          tabIndex="0"
          src={closeIcon} 
          onClick={() => closeView()}
        />
        <div className="content" role="article">
          {content}
        </div>
      </div>
    </div>
  )
}